<template>
  <div>
    <p>반갑습니다. {{ this.getUserInfo }} 님</p>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions } from 'vuex'



export default {
  data(){
    return {
      name: null,
    };
  },
  methods : {
    ...mapActions(['updateUser','initializeSections']),
    async getInfo(){
      try {
        console.log(this.accessToken)
        const response = await axios.get("/v1/nid/me", {
          headers: {
            Authorization: `Bearer ${this.accessToken}`, // Access Token을 Authorization 헤더에 포함
          },
        });
        const userInfo=response.data.response.birthyear+response.data.response.name
        this.$store.dispatch('updateUser', userInfo);

        this.setCookie("name", userInfo)        // 로그인 인포 쿠키저장        

        window.location.href = "/new.io";
      } catch (error) {
        console.error("사용자 정보 가져오기 실패:", error);
        window.location.href = "/new.io";
      }
    },
    setCookie(name, value, days) {
    //const date = new Date();
    //date.setTime(date.getTime() + days * 24 * 60 * 60 * 1000);
    document.cookie = `${name}=${value};path=/`;
    },
  },
  mounted() {
    console.log("로그인시작")
    const naver_id_login = new window.naver_id_login(
      {clientId:"3UpoleTgLO4RWnFSsh8p",
       callbackUrl: "https://kimin-kim3927.github.io/new.io/login/naver",
       referrerPolicy: "strict-origin-when-cross-origin",
      });
    const accessToken =naver_id_login.getAccessToken()
    console.log("12")
    console.log("access token", accessToken); // 정상적 로그인이 된 경우 access token값 출력
    this.accessToken = accessToken
    this.getInfo()
    console.log("액세스")
  }
}

</script>